<template>
    <Phone>
            <div class="topbar">
                <div>
                    <div class="clock">22:12</div>
                    <div class="notIcons">
                        <img src="https://i.ibb.co/b6p3Z4N/not-icons.png" alt="not-icons" border="0">
                    </div>
                </div>
                
                <router-link  :to="{name:'chatslist'}"> <i class="fas fasArrow fa-chevron-left fa-2x"></i> </router-link>
                <h2 class='titlechats'>Informaci√≥n</h2>
            </div>

            <div class='settingslist'>
                <div class='home'>
                    {{this.msg.length}}
                    <button @click="stopper()"> Stop </button>
                    <button @click="printer()"> Start </button>
                    <div v-for="index in this.msg.length" :key="index">
                        {{setText(this.msg[index-1].text)}}
                        <h1 v-html="paper"> </h1>
                        {{this.titleText}}
                    </div>
                </div>

                <div>
                    <button @click="show = !show"> Toggle render </button>
                    <transition name="fade">
                        <p v-if="show">hello</p>
                    </transition>
                    <button :style="btnStyles">My button</button>
                </div>
            </div>
    </Phone>
</template>

<script>
export default({
    name: 'Home',
    
    data() {
        return{
            titleText:'Hello, Typewritter can type anything -----',
            paper:'',
            timer: null,
            show: true,
            msg: [
                {text: 'Holaaa'},
                {text: 'Que tal?'},
                {text: 'Coomo estas??'}
            ],

        }
    },
    props: {
        bgColor: {
        type: String,
        default: "#07C286"
        },
        height: {
        type: Number,
        default: 100
        }
    },
    mounted(){
        //this.printer()
    },
    computed: {
        btnStyles() {
        return {
            "background-color": "#07C286",
            height: `${this.height}px`
        };
        }
    },
    methods: {
        printer(){
            this.paper = ''
            this.stopper()
            let i = -1
            this.timer = setInterval(() =>{
                i++
                const x = i%this.titleText.length
                this.paper += this.titleText[x]
            }, 100-(Math.random()*50))

        },
        stopper(){
            clearInterval(this.timer)
        },
        setText(text){
            this.titleText = text;
        }
    },
    watch:{
        paper:function(){
            if(this.paper.length == this.titleText.length){
                this.stopper()
            }
        }
    }
})


</script>

<style scoped>

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

h1{
    border-right: 2px black solid;
    animation: typebar 0.s steps(40) infinite;
}
@keyframes typebar{
    from{border-right: 2px black solid;}
    to{border-right: 2px transparent solid;}
}
.cardSettings{
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 20px; /* 5px rounded corners */
    border: 0px solid black;
    margin: 10px;
    text-align: left;
    padding: 10px;
}
.textSettings{
    margin: 0px 0px 0px 25px;
    font-size: 20px;
}
.backgroundImage{
    width: 90px;
    height: 90px;
    background-size: 100%;
    border-radius: 30px;
    margin: 7px
}
.backgroundImage:hover{
    box-shadow: 0 4px 8px 0 rgba(87, 66, 66, 0.35);
    border: 2px solid var(--main-color)
}
.settingslist{
    padding-top: 20px;
    min-height: 480px;
}
</style>
